.frontend-container{
  position: relative;
  float: left;
  padding: 3% 5%;
  padding-right: 2vw;
  min-height: 60vh;
  border-radius: 5px;
  margin: 3vh 3.5vw;
  border: solid 2px;
  border-image: url(./images/bestgoldleaf.jpg) 4 4;
  font-family: 'Italiana', serif;
}

.backend-container{
  position: relative;
  float: left;
  padding: 3% 5%;
  min-height: 60vh;
  border-radius: 5px;
  margin: 3vh 3.5vw;
  border: solid 2px;
  border-image: url(./images/bestgoldleaf.jpg) 4 4;
  font-family: 'Italiana', serif;
