  padding-right: 2vw;

}

.about-container{
  width: 72vw;
  font-family: 'Raleway Dots', cursive;
  position: absolute;
  top: 17vh;
  left: 13vw;
  padding: 0 11vw;
  border: 2px solid black;
  border-image: url(./images/bestgoldleaf.jpg) 4 4;
  border-top-style: none;
  border-bottom-style: none;
}

.about-container h3{
  font-family: 'Raleway Dots', cursive;
  color: black;
  line-height: 1.7;
  text-align: center;
  font-size: 1.2rem;
}

.contact-container{
  position: absolute;
  padding: 3%;
  top: 0vh;
  left: 30vw;
  /* background-color: rgba(71, 70, 70, 0.1); */
}

.contact-container h3{
  font-family: 'Raleway Dots', cursive;
  line-height: .8;
}

.contact-container a{
  padding-left: 20px;
  color: rgb(146, 142, 134);
  /* text-decoration: underline; */
}

.contact-container a:hover{
  color: rgb(160, 143, 49);
  text-decoration: underline;
}

.contact-container input, textarea, button{
  margin-top: 5px;
  padding: 7px;
  border-radius: 3px;
  border-style: none;
  background-color: rgba(78, 77, 77, 0.1);
}

.input-text{
  width: 80%;
}

.submit-button{
  padding: 7px;
  cursor: pointer;
}


.contact-icons{
  margin-right: 10px;
  height: 13px;
  margin-left: -2vw;
}

.side-tabs{
  width: 19vw;
  height: 100vh;
  position: fixed;
  margin-top: -7vh;
}

.tab{
  margin: 6vh 0vw;
  padding: 2vh 3vw;
  padding-right: 1vw;
  cursor: pointer;
  opacity: .8;
  color: rgb(170, 169, 169);
  /* text-shadow: 1px 0 rgb(161, 159, 159); */
  border-radius: 4px;
  background: url(./images/pointbanner1.png);
  background-position: center left;
  background-repeat: no-repeat;
  background-size: 100% 85%;
}

.tab h3{
  font-family: 'Raleway Dots', cursive;
  text-shadow: .5px 0 rgb(77, 76, 76);

}

.tab:hover{
  opacity: 1;
  color: black;
}

.selected-tab{
  background: url(./images/sidetabcircuits2.png),url(./images/pointbanner1.png);
  opacity: 1;
  /* background-size: 95%; */
  background-size: 95% 180%, 100% 90%;
  background-repeat: no-repeat, no-repeat;
  background-position: center left, center left;
  border-right: hidden;
  color: rgb(43, 42, 42);
  
}

.display-project-container{
  position: absolute;
  top: 17vh;
  left: 25vw;
  min-height: 70vh;
  width: 70vw;
  /* border: 1px solid black; */
  padding: 10px;
}

.brakebills-container{
  margin-bottom: 13vh;
}

.brakebills-container iframe{
  margin-bottom: 5vh;
  background-size: fill;
  
}

.brakebills-description{
  margin-top: 7vh;
  margin-bottom: 5vh;
  font-size: 1.2rem;
  width: 60vw;
  padding-left: 5vw;
  font-family: 'Raleway Dots', cursive;
  color: black;
  text-align: center;
  

}

